.posts-list {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	grid-gap: 10px;
	list-style: none;
	padding: 0;
	margin: 0;
}

.post {
	border: 2px solid navy;

	&__img img {
		width: 100%;
	}

	&__info {
		display: flex;
		gap: 10px;
		font-family: sans-serif;
		align-items: center;
		padding: 10px;
	}

	&__title {
		color: navy;
		text-transform: uppercase;
		margin: 0;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	&__user-id {
		color: green;
		font-size: 12px;
		margin: 0;
		width: 100px;
		flex-shrink: 0;
	}

	&__text {
		padding: 10px;
		max-height: 65px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}
}